:root {
  --color-bg: #5aaae2;
  --color-bg-variant: #5073d45d;
  --color-bg-variant1: #5073d4;
  --color-primary: #5aaae2;
  --color-primary-trans: #5aa9e2a6;
  --color-primary-variant: rgba(62, 152, 216, 0.4);
  --color-white: #fff;
  --color-light: rgb(255, 255, 255);

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;  
  background-image: radial-gradient(#082027b2 2px, transparent 2px), radial-gradient(#082027b2 2px, transparent 2px);
  background-size: 18px 18px;
  background-position: 0 0, 9px 9px;
  background-color: #050d1f;
}


/* GENERAL STYLES *//* GENERAL STYLES *//* GENERAL STYLES *//* GENERAL STYLES */
/* GENERAL STYLES *//* GENERAL STYLES *//* GENERAL STYLES *//* GENERAL STYLES */

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}
section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
  font-size: 2rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white);  
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background-color: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background-color: var(--color-primary);
  color: var(--color-white);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN */
/* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN *//* RESPONSIVE DESIGN */
@media screen and (max-width: 1024px) {
  .container{
    width: var(--container-width-md);
  }  

  section{
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container{
    width: var(--container-width-sm);
  }  

  section > h2{
    margin-bottom: 2rem;
  }
}